  max-height: '270px';
  height: '270px';
  padding: '0px'
}

@keyframes fishMovement {
  60% {
    transform: translate(-50%, 50%);
  }

  65% {
    transform: translate(50%, 50%);
  }

  100% {
    transform: translate(100px, 50%);
  }
}

@keyframes boatMovement {
  30% {
    transform: rotate(-5deg);
  }

  60% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes twinkling {
  30% {
    opacity: 0.4;
  }

  60% {
    transform: scale(1.2);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes rotateSun {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes swingCloud {
  50% {
    transform: translateX(50px);
  }
}

